.home {
  display: flex;
  &__wrapper {
    width: calc(100% - em(170));
    height: 100%;
    max-width: em(1750);
    background: url('../../assets/pages/home/bg-home.png') no-repeat left
      top/100%;
  }

  &__entry {
    position: absolute;
    top: 39.7vw;
    left: 53.64vw;
    width: em(136);
    height: em(87);
    background: url('../../assets/pages/home/entry.png') no-repeat center/100%;
    animation: entry 1s alternate-reverse infinite;
  }
  .info {
    position: absolute;
    left: em(100);
    bottom: em(-40);
    width: em(440);
    height: em(460);
    border-radius: em(32);
    border: em(4) solid $black;
    background-color: $white;
    &__title {
      position: absolute;
      left: em(32);
      top: em(-32);
      display: inline-block;
      padding: em(20) em(32) em(25);
      background: url('../../assets/frames/title-yellow.png') no-repeat
        center/100%;
      p {
        color: $black;
        font-size: $f__24;
        font-weight: bold;
      }
    }
    &__container {
      padding: em(50) em(54) em(50) em(32);
    }
    &__link {
      position: relative;
      display: block;
      text-align: left;
      margin: em(20) 0;
      p {
        position: relative;
        z-index: 9;
        font-size: $f__24;
        line-height: 2;
        font-weight: bold;
      }
      span {
        font-size: $f__16;
      }
      &:hover {
        &::before {
          z-index: 0;
          @include pseudo-element;
          width: 100%;
          height: 16px;
          top: 20px;
          left: 0;
          background-color: $yellow;
        }
        &::after {
          @include pseudo-element;
          top: 50%;
          left: -70px;
          width: 60px;
          height: 60px;
          background: url('../../assets/buttons/btn-right-round.png') no-repeat
            center/100%;
          transform: translateY(-50%);
        }
      }
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .home {
    font-size: 0.8em;
  }
}

@media screen and (max-width: $pad-width) {
  .home {
    font-size: 0.6em;
  }
}
