.b-section {
  width: 100%;
  padding: em(20);
  text-align: left;
  background-color: $black;
  &--full {
    padding: em(20) 0;
  }
  &--bottom {
    position: fixed;
    max-width: $desktop-width;
    margin: auto;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 30%);;
  }
}
