@charset "UTF-8";

$pad-width: 1200px;
$laptop-width: 1400px;
$desktop-width: 641px;
$max-desktop-height: 750px;

//fonts
@font-face {
  font-family: 'GenSenMaruGothicTW';
  src: url('../../fonts/GenSenMaruGothicTW-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'GenSenMaruGothicTW';
  font-weight: bold;
  src: url('../../fonts/GenSenMaruGothicTW-Bold.ttf') format('truetype');
}
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@400;500;700&family=Noto+Serif+SC:wght@200&display=swap');
$font-family-base: 'GenSenMaruGothicTW', 'Noto Sans TC', 'Noto Sans',
  'Hiragino Sans GB', arial, sans-serif;

//font size
$f__10: em(10);
$f__12: em(12);
$f__14: em(14);
$f__16: em(16);
$f__18: em(18);
$f__20: em(20);
$f__24: em(24);
$f__26: em(26);
$f__28: em(28);
$f__30: em(30);
$f__36: em(36);
