*::before,
*::after {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  outline: none;
  vertical-align: baseline;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote {
  &:before,
  &:after {
    content: '';
    content: none;
  }
}

q {
  &:before,
  &:after {
    content: '';
    content: none;
  }
}

input {
  border: 0;
  border-radius: 0;
  border-color: transparent;
  box-shadow: none;
  outline: none;
  appearance: none;
  -webkit-appearance: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

select {
  -webkit-appearance: none;
  appearance: none;
  outline: none;
}

a:link {
  color: #555;
  //設定還沒有瀏覽過的連結
  text-decoration: none;
  outline: none;
  cursor: pointer;
}
a:visited {
  //設定已經瀏覽過的連結
  color: #555;
  background-color: none;
}
a:hover {
  color: #555;
  //設定滑鼠移經的連結
  text-decoration: none;
  background-color: none;
}
a:active {
  color: #555;
  //設定正在點選的連結
  text-decoration: none;
  background-color: none;
}
