@charset "UTF-8";

//顏色色系
$red: #ff0000;
$blue: #428bca;
$white: #ffffff;
$black: #000000;
$pink: #f04ea7;
$gray: #939393;
$gray-light: #c9c9c9;
$gray-light--2: #616161dd;
$gray-dark: #1d1d1d;
$yellow: #ffff62;
$green: #c9ff71;

//抽象命名
$btn-primary-color: $white;
$btn-primary-bg: $blue;
$btn-primary-border: darken($blue, 5%);

$btn-second-color: $white;
$btn-second-bg: $blue;
$btn-second-border: darken($blue, 5%);

$btn-third-color: $white;
$btn-third-bg: $blue;
$btn-third-border: darken($blue, 5%);
