.b-input {
  &__text {
    display: block;
    width: 100%;
    padding: em(5) 0;
    color: $white;
    font-size: $f__16;
    font-weight: 700;
    background-color: transparent;
    border-bottom: 1.5px solid $white;
    &--password {
      background: url(/assets/icons/input/password.png) no-repeat right/$f__20;
    }
    &--error {
      border-bottom: 1.5px solid $red;
    }
  }
  &__date {
    display: block;
    width: 100%;
    padding: em(5) 0;
    border-bottom: 1.5px solid $white;
    &--error {
      border-bottom: 1.5px solid $red;
    }
  }
  &__select {
    color: $white;
    font-size: $f__16;
    font-weight: 700;
    border: none;
    padding: 0 em(32) 0 em(16);
    background-color: transparent;
    &-wrapper {
      position: relative;
      display: inline-block;
      &::after {
        @include pseudo-element;
        display: block;
        content: '▼';
        top: 20%;
        right: 1.5em;
        font-size: $f__12;
        transform: scale(0.5);
      }
    }
  }
  &__option {
    color: $black;
  }
  &__checkbox {
    position: relative;
    display: inline-block;
    font-size: $f__16;
    width: em(24);
    height: em(24);
    border: em(2) solid $white;
    border-radius: em(6);
    &:checked {
      &::after {
        @include pseudo-element;
        top: em(-3);
        left: em(-3);
        width: em(26);
        height: em(26);
        background: url(/assets/icons/input/check.png) no-repeat center/$f__12
          $f__10;
      }
      & + .b-input__checkbox-label {
        background-color: $gray-light--2;
      }
    }
    &-label {
      font-size: $f__16;
      padding: em(10) em(20);
      &--round {
        border: em(1) solid $gray-light--2;
        border-radius: em(15);
      }
      &--full {
        width: 90%;
      }
      &--receipt {
        display: grid;
        grid-template-columns: 25% 75%;
        grid-row-gap: em(10);
        grid-column-gap: em(15);
      }
    }
    &-title {
      font-size: $f__14;
      font-weight: lighter;
    }
    &-value {
      font-size: $f__16;
      font-weight: 500;
    }
    &-submit {
      color: $black;
      background-color: $white;
      &:link,
      &:visited,
      &:active {
        color: $black;
      }
    }
  }
  &__tips {
    margin-top: em(5);
    color: $red;
    font-size: $f__10;
    font-weight: lighter;
  }
}
