.lobby {
  position: relative;
  display: flex;
  &__wrapper {
    width: calc(100% - em(170));
    height: 100%;
    max-width: em(1750);
    background: url('../../assets/pages/lobby/bg-lobby.png') no-repeat right
      bottom/auto 100%;
  }
  &__title {
    position: absolute;
    left: em(5);
    bottom: em(-30);
    width: em(420);
    height: em(120);
    img {
      width: 100%;
    }
  }
  .info {
    position: absolute;
    left: em(80);
    top: em(160);
    width: em(560);
    height: em(764);
    border-radius: em(32);
    background: url('../../assets/frames/info-basic.png') no-repeat center/ 100%;
    &__container {
      text-align: left;
      padding: em(220) em(48) 0 em(48);
    }
    &__index {
      p {
        font-size: $f__16;
        font-weight: bold;
      }
    }
    &__title {
      margin: em(8) 0 em(24);
      span {
        position: relative;
        font-size: $f__24;
        z-index: 9;
        line-height: 2;
        font-weight: bold;
      }
      p {
        display: inline-block;
        position: relative;
        &::before {
          z-index: 0;
          @include pseudo-element;
          width: 100%;
          height: 16px;
          top: 30px;
          left: -8px;
          background-color: $green;
        }
      }
    }
    &__content {
      padding-right: em(20);
      p {
        font-size: $f__16;
        line-height: 2;
      }
    }
    &__cta {
      position: absolute;
      @include flex-jcc-atc;
      right: em(60);
      bottom: em(60);
      p {
        font-size: $f__16;
        margin-right: em(16);
      }
      img {
        width: em(10);
      }
    }
  }
  &__question {
    position: absolute;
    display: block;
    width: em(200);
    height: em(200);
    &--1 {
      top: 40%;
      left: 35%;
    }
    &--2 {
      top: 20%;
      left: 50%;
    }
    &--3 {
      top: 15%;
      right: 15%;
    }
    &--4 {
      top: 40%;
      right: 15%;
    }
  }
}

@media screen and (max-width: $laptop-width) {
  .lobby {
    font-size: 0.8em;
    .info {
      font-size: 0.9em;
    }
  }
}

@media screen and (max-width: $pad-width) {
  .lobby {
    font-size: 0.6em;
  }
}
