.b-btn {
  min-width: em(150);
  display: block;
  font-size: em(16);
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
  border-radius: em(30);
  padding: em(10) em(30);
  background-color: $white;
  border: em(2) solid $black;
  &,
  &:link,
  &:visited {
    color: $black;
  }
  &:active {
    color: $white;
    background-color: $black;
  }

  &--disable {
    background-color: $gray;
    &,
    &:link,
    &:active,
    &:visited {
      color: $gray-light;
    }
  }
  &--white {
    background-color: $white;
    &,
    &:link,
    &:visited {
      color: $black;
    }
    &:active {
      color: $white;
      background-color: $gray-dark;
      border: em(2) solid $gray-dark;
    }
  }
}
