@for $i from 0 through 12 {
  .u-m-#{$i} {
    margin: #{em($i * 10)} !important;
  }
  .u-mt-#{$i} {
    margin-top: #{em($i * 10)} !important;
  }
  .u-mb-#{$i} {
    margin-bottom: #{em($i * 10)} !important;
  }
  .u-ml-#{$i} {
    margin-left: #{em($i * 10)} !important;
  }
  .u-mr-#{$i} {
    margin-right: #{em($i * 10)} !important;
  }
  .u-mlr-#{$i} {
    margin-left: #{em($i * 10)} !important;
    margin-right: #{em($i * 10)} !important;
  }
  .u-mlr-auto {
    margin: 0 auto;
  }
  .u-mtb-#{$i} {
    margin-top: #{em($i * 10)} !important;
    margin-bottom: #{em($i * 10)} !important;
  }
}
@for $i from 0 through 12 {
  .u-pt-#{$i} {
    padding-top: #{em($i * 10)} !important;
  }
  .u-pb-#{$i} {
    padding-bottom: #{em($i * 10)} !important;
  }
  .u-pl-#{$i} {
    padding-left: #{em($i * 10)} !important;
  }
  .u-pr-#{$i} {
    padding-right: #{em($i * 10)} !important;
  }
  .u-plr-#{$i} {
    padding-left: #{em($i * 10)} !important;
    padding-right: #{em($i * 10)} !important;
  }
  .u-ptb-#{$i} {
    padding-top: #{em($i * 10)} !important;
    padding-bottom: #{em($i * 10)} !important;
  }
}
.u-text {
  &-lowercase {
    text-transform: lowercase !important;
  }
  &-uppercase {
    text-transform: uppercase !important;
  }

  &-capitalize {
    text-transform: capitalize !important;
  }
  &-justify {
    text-align: justify !important;
    font-style: oblique;
  }
  &-wrap {
    white-space: normal !important;
  }

  &-nowrap {
    white-space: nowrap !important;
  }
  &-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &-left {
    text-align: left !important;
  }

  &-right {
    text-align: right !important;
  }

  &-center {
    text-align: center !important;
  }
  &-height--15 {
    line-height: 1.5;
  }
  &-height--25 {
    line-height: 1.25;
  }
  &-height--75 {
    line-height: 1.75;
  }
  &-height--2 {
    line-height: 2;
  }
}

.u-font {
  &-weight-light {
    font-weight: 300 !important;
  }
  &-weight-lighter {
    font-weight: lighter !important;
  }

  &-weight-normal {
    font-weight: 400 !important;
  }

  &-weight-bold {
    font-weight: 700 !important;
  }

  &-weight-bolder {
    font-weight: bolder !important;
  }

  &-italic {
    font-style: italic !important;
  }
  &-min {
    font-size: $f__12 !important;
  }
  &-xxs {
    font-size: $f__14 !important;
  }
  &-xs {
    font-size: $f__18 !important;
  }
  &-sm {
    font-size: $f__20 !important;
  }
  &-md {
    font-size: $f__24 !important;
  }
  &-lg {
    font-size: $f__26 !important;
  }
  &-xl {
    font-size: $f__28 !important;
  }
  &-xxl {
    font-size: $f__30 !important;
  }
  &-white {
    color: $white !important;
  }
  &-yellow {
    color: $yellow !important;
  }
  &-green {
    color: $green !important;
  }
  &-black {
    color: $black !important;
  }
}

.u-opt {
  &-0 {
    opacity: 0;
  }
  &-25 {
    opacity: 0.25;
  }
  &-75 {
    opacity: 0.75;
  }
  &-100 {
    opacity: 1;
  }
}

.u-d {
  &-none {
    display: none !important;
  }
  &-block {
    display: block !important;
  }
  &-flex {
    display: flex !important;
  }
  &-inlineflex {
    display: inline-flex !important;
  }
  @media (min-width: 0px) {
    &-sm {
      &-none {
        display: none !important;
      }
      &-block {
        display: block !important;
      }
      &-flex {
        display: flex !important;
      }
      &-inlineflex {
        display: inline-flex !important;
      }
    }
  }
  @media (min-width: 768px) {
    &-md {
      &-none {
        display: none !important;
      }
      &-block {
        display: block !important;
      }
      &-flex {
        display: flex !important;
      }
      &-inlineflex {
        display: inline-flex !important;
      }
    }
  }
  @media (min-width: 1200px) {
    &-lg {
      &-none {
        display: none !important;
      }
      &-block {
        display: block !important;
      }
      &-flex {
        display: flex !important;
      }
      &-inlineflex {
        display: inline-flex !important;
      }
    }
  }
}
