.b-alert {
  @include flex-jcc-atc;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: em(20);
  background-color: rgba(0, 0, 0, 0.85);
  &__container {
    @include flex-jcc-atc;
    flex-direction: column;
    width: 100%;
    max-width: $desktop-width;
    padding: em(40) em(10);
    &--white {
      color: $black;
      background-color: $white;
      p {
        color: $black;
      }
    }
    &--round {
      border-radius: em(10);
    }
  }
  &__line {
    display: block;
    width: 100%;
    height: em(1);
    margin-top: em(10);
    background-color: $gray-light;
  }
  &__content {
    text-align: left;
    img {
      width: 100%;
      margin: em(15) 0;
    }
  }
  &__head {
    display: flex;
    width: 100%;
  }
  &__title {
    @include flex-jcc-atc;
    height: 100%;
    padding: 0 em(5);
    text-align: left;
    &--sm {
      font-size: $f__14;
      font-weight: lighter;
    }
    &--lg {
      font-size: $f__24;
      font-weight: bold;
    }
  }
  &__text {
    @include flex-jcc-atc;
    padding: em(10);
    font-size: $f__14;
    a {
      color: $black;
      font-weight: bold;
      text-decoration: underline;
    }
  }
  &__exclamation {
    width: em(65);
    height: em(65);
    background: url(/assets/icons/alert/exclamation.png) no-repeat center/100%;
  }
  &__question {
    width: em(65);
    height: em(65);
    background: url(/assets/icons/alert/question.png) no-repeat center/100%;
  }
  &__oops {
    width: em(65);
    height: em(65);
    background: url(/assets/icons/alert/oops.png) no-repeat center/100%;
  }
  &__step {
    display: flex;
    align-items: center;
    img {
      width: em(100);
    }
  }
}
