.fade-enter-active,
.fade-leave-active {
  transition: all 1s;
  transition: opacity 0.8s !important;
}
.fade-enter,
.fade-enter-from,
.fade-leave-to {
  transition: all 1s;
  opacity: 0 !important;
}
.fade-enter-from,
.fade-leave-to {
  transition: all 1s;
}
.fade-enter-to,
.fade-leave-from {
  transition: all 1s;
  transform: translateY(0%);
}

@keyframes showTop {
  0% {
    opacity: 0;
    transform: translateY(20%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes showDown {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes hideTop {
  0% {
    opacity: 1;
    transform: translateY(20%);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
@keyframes hideDown {
  0% {
    opacity: 1;
    transform: translateY(-20%);
  }
  100% {
    opacity: 0;
    transform: translateY(0);
  }
}
@keyframes toShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes toHide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes searchLights {
  0% {
    left: -em(240);
  }
  100% {
    left: em(240);
  }
}
@keyframes absoluteScale {
  from {
    transform: translateX(-50%) scale(1.05);
  }
  to {
    transform: translateX(-50%) scale(0.95);
  }
}
@keyframes Scale {
  from {
    transform: scale(1.05);
  }
  to {
    transform: scale(0.95);
  }
}

@keyframes shaking {
  from {
    margin: 2% 0;
  }
  to {
    margin: -2% 0;
  }
}

@keyframes rolling {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes entry {
  from {
    transform: translate(-10%, -10%);
  }
  to {
    transform: translate(10%, 10%);
  }
}
